@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
body {
  z-index: -1;
  background: url(/starwars-datatable/static/media/background.280e8467.png);
  font-family: 'Rubik', sans-serif;
  margin: 0;
  padding: 0;
}

body {
  color: rgb(247, 247, 248);
  text-shadow: 0px 0px 10px rgb(0, 0, 0);
}

table, th, td {
  border: 1px solid rgb(255, 238, 84);
  font-size: 1em;
  line-height: 120%;
  max-width: 99%;
  margin-left: 0.3%;
  text-align: center;
  font-weight: bold;
}

th {
  text-shadow: 0px 0px 10px rgb(255, 255, 255);
}

/* td {
  border-bottom: 1px solid rgb(255, 238, 84);
  border-right: 1px solid rgb(255, 238, 84);
} */

.inputName {
  font-size: 0.7em;
  background-color: rgba(42, 41, 39, 0.9);
  color: white;
  box-shadow: 0px 0px 10px rgb(255, 255, 255);
  margin-left: 0.8%;
  margin-bottom: 0.8%;
}

.labelName {
  margin-left: 0.8%;
  font-size: 1.4em;
}

.image {
  display: flex;
  align-items: center;
}

img {
  max-width: 100%;
}

img:hover {
  transform: scale(1.1);
}
.inputComparison {
  font-size: 1.1em;
  background-color: rgba(42, 41, 39, 0.9);
  color: white;
  box-shadow: 0px 0px 10px rgb(255, 255, 255);
  margin-left: 0.8%;
  margin-bottom: 0.8%;
}

.select {
  font-size: 1.2em;
  background-color: rgba(42, 41, 39, 0.9);
  color: white;
  box-shadow: 0px 0px 10px rgb(255, 255, 255);
  margin-left: 0.8%;
  margin-bottom: 0.8%;
}

button {
  font-size: 1.2em;
  background-color: rgba(42, 41, 39, 0.9);
  color: white;
  box-shadow: 0px 0px 10px rgb(255, 255, 255);
  margin-left: 0.8%;
  margin-bottom: 0.8%;
}

